import React, { Component } from "react";
import { Route, HashRouter } from "react-router-dom";
import { connect } from "react-redux";
import L from "react-loadable";
import LaunchesPage from './Launches/LaunchesPage';
import LaunchPage from "./Launch/LaunchPage";

import api from "./apis/LaunchLibrary";

class MyRouter extends Component {
  async componentDidMount() {
    const launches = await api.launches();
    this.props.addLaunches(launches);
  }

  render() {
    return (
      <HashRouter ref={r => (this.router = r)}>
        <div>
          <Route exact path="/" component={LaunchesPage} />
          <Route
            path="/launch/:id"
            component={LaunchPage}
            onUpdate={() => window.scrollTo(0, 0)}
          />
          <div id="footer">
            API provided by{" "}
            <a href="https://launchlibrary.net/">LaunchLibrary</a>
          </div>
        </div>
      </HashRouter>
    );
  }
}

const mapStateToProps = ({ data: { launches } }) => ({
  launches
});

const mapDispatchToProps = dispatch => ({
  addLaunches: value => dispatch({ type: "ADD_LAUNCHES", value })
});

export default connect(mapStateToProps, mapDispatchToProps)(MyRouter);



