import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import Launch from "./Launch";
import logo from "../logo.png";

import Preloader from "../ui/Preloader";

import posed from "react-pose";

const Appear = posed.div({
  closed: { opacity: 1, y: '-5px' },
  open: { opacity: 1, y: '0' }
})

const Group = posed.div({
  open: {
    staggerChildren: 80
  }
})

class LaunchesPage extends Component {

  state = { pose: this.props.launches.length ? 'open' : 'closed' }

  renderLaunches = () => {
    const pose = this.state.pose;
    console.log(pose);
    return <Group pose={pose}>{this.props.launches.map(launch => (
      <Appear key={launch.id}>
        <Launch data={launch} />
      </Appear>
    ))}
    </Group>
  };

  componentDidUpdate() {
    if(this.state.pose === 'closed' && this.props.launches.length) {
      this.setState({pose: 'open'})
    }
  }

  render() {
    return (
      <div>
        <div className="welcome">
          <div className="welcome-content">
            <div className="welcome-logo">
              <Link to="/">
                <img
                  alt="logo"
                  src={logo}
                  height="24"
                  style={{ verticalAlign: "middle" }}
                />{" "}
                Liftoff
              </Link>
            </div>
            <div className="welcome-title">Rocket Launches</div>
          </div>
        </div>
        <div className="container">
          {this.renderLaunches()}
          {this.props.launches.length ? "" : <Preloader />}
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ data: { launches } }) => ({
  launches
});

export default connect(mapStateToProps)(LaunchesPage);



